<template>
  <div :class="$style.content">
    <div :class="[$style.item, $style.title]">Daily</div>
    <img
      v-if="isWorkingDay"
      :class="$style.photo" :src="winner.img"/>
    <br>
    <div
      v-if="isWorkingDay"
      :class="[$style.item, $style.title]">¡Felicitaciones!</div>
    <br>
    <div
      :class="$style.groups">
      <div
        v-for="(mem, index) in filteredMembers"
        :key="index"
        :class="[
          $style.groupsItem,
          next.name === mem.name && $style.next]">
        <img :class="$style.groupsItemPhoto" :src="mem.img"/>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import members from '@/utils/members.js';

const START_DATE = moment('2021-01-01', 'YYYY-MM-DD');

export default {
  name: 'Daily',
  props: {
    selected: {
      type: Object,
      default: () => moment(),
    },
  },
  computed: {
    isWorkingDay() {
      return !(this.selected.weekday() === 5 || this.selected.weekday() === 6);
    },
    totalBusinessDays() {
      let total = 0;
      const d1 = moment(START_DATE);
      while (d1.isBefore(this.selected, 'date')) {
        total += 1;
        // TODO: discount feriados
        if (d1.weekday() === 5) {
          total -= 1;
        }
        if (d1.weekday() === 6) {
          total -= 1;
        }
        d1.add(1, 'd');
      }
      return total;
    },
    filteredMembers() {
      return members.filter(x => x.groups.includes('daily'));
    },
    winner() {
      return this.filteredMembers[this.totalBusinessDays%this.filteredMembers.length];
    },
    next() {
      return this.filteredMembers[(this.totalBusinessDays+1)%this.filteredMembers.length];
    },
  },
  methods: {
    moment,
  },
  data() {
    return {
      members,
    };
  },
  created() {
    moment.locale('es');
  },
};
</script>

