<template>
  <div :class="$style.content">
    {{ headerText }}
  </div>
</template>

<script>
import moment from 'moment';

export default {
  name: 'DayHeader',
  props: {
    selected: {
      type: Object,
      default: () => moment(),
    },
  },
  computed: {
    headerText() {
      return `${this.selected.format('dddd')} ${this.selected.format('D')} de ${this.selected.format('MMMM')} del ${this.selected.format('YYYY')}`;
    },
  },
  methods: {
  },
  data() {
    return {
    };
  },
  created() {
    moment.locale('es');
  },
};
</script>

