<template>
  <div :class="$style.content">
    <!-- Header -->
    <div :class="$style.week">
      <div
        v-for="h in header"
        :key="`${h}-header`"
        :class="$style.header">
        <div :class="$style.dayContent">{{h}}</div>
      </div>
    </div>
    <!-- Grid -->
    <div
      v-for="w in weeks"
      :key="`${w}-week`"
      :class="$style.week">
      <div
        v-for="d in days"
        :key="`${d}-day`"
        :class="$style.day"
        @click="selectEvent.bind(null, d)">
        <div :class="$style.dayHeader">
          <div :class="$style.dayHeaderNumber">
            {{ dates[w*7 + d].date.format('D') }}
          </div>
          <div :class="$style.dayHeaderFill">
          </div>
        </div>
        <div :class="$style.dayContent">{{ dates[w*7 + d].date.format('DD-MM-yyyy') }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment';

export default {
  name: 'Grid',
  props: {
    dates: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    header() {
      return moment.weekdays(true);
    },
    weeks() {
      return [...Array(5).keys()];
    },
    days() {
      return [...Array(7).keys()];
    },
  },
  methods: {
    selectEvent({date}) {
      this.$emit('select', date);
    },
  },
  data() {
    return {
    };
  },
  created() {
    moment.locale('es');
  },
};
</script>

<style module lang="scss">
.content {
  display: flex;
  flex-direction: column;
}
.week {
  display: flex;
  flex-direction: row;
  border-top: 1px silver solid;
  border-bottom: 1px silver solid;
}
.header {
  display: flex;
  font-size: 0.75em;
  flex: 1;
  border-left: 1px silver dotted;
  border-right: 1px silver dotted;
  justify-content: center;
}
.day {
  display: flex;
  flex-direction: column;
  flex: 1;
  border-left: 1px silver solid;
  border-right: 1px silver solid;
  height: 7em;
}
.dayHeader {
  display: flex;
}
.dayHeaderNumber {
  flex: 1;
  text-align: center;
  font-size: 1.5em;
  box-shadow: 1px 1px 1px 0 silver;
  background-color: yellow;
}
.dayHeaderFill {
  display: flex;
  flex: 7;
}
.dayContent {
  justify-content: center;
  display: flex;
}
</style>