<template>
  <div :class="$style.app">
    <day-header :selected="selected"/>
    <div :class="$style.body">
      <birthdays :selected="selected" style="display: flex;"/>
      <daily :selected="selected" style="display: flex;"/>
      <memos :selected="selected" style="display: flex; flex: 2;"/>
      <picker style="display: flex; flex: 3;"/>
    </div>
    <div :class="$style.footer">
      <timeline
        :selected="selected"
        :dates="month"
        @select="selectDate"/>
    </div>
  </div>
</template>

<script>
import moment from 'moment';

import Grid from '@/components/Grid'
import DayHeader from '@/components/DayHeader'
import Timeline from '@/components/Timeline'
import Birthdays from '@/components/Birthdays'
import Daily from '@/components/Daily'
import Memos from '@/components/Memos'
import Picker from '@/components/Picker'

export default {
  name: 'App',
  components: {
    Grid,
    DayHeader,
    Timeline,
    Birthdays,
    Daily,
    Memos,
    Picker,
  },
  computed: {
    dataset() {
      const firstDay = moment().set('date', 1);
      return [...Array(5 * 7).keys()].map(x => ({
        date: moment(firstDay).add(x, 'd'),
        items: [],
      }));
    },
    month() {
      const firstDay = moment().set('date', 1);
      return [...Array(moment().daysInMonth())]
        .map((x, i) => ({
          date: moment(firstDay).add(i, 'd'),
          items: [],
        }));
    },
  },
  methods: {
    selectDate(date){
      this.selected = moment(date);
    },
  },
  data() {
    return {
      selected: moment(),
    };
  },
  created() {
    moment.locale('es');
    this.selected = moment()
  },
};
</script>

