  font-size: 1.2em;
  justify-content: center;
}
.photoDiv {
  margin: 0;
  padding: 0;
}
.birthday {
  position: relative;
  &::after{
    background: url("../assets/hat.webp") no-repeat scroll 0 0 transparent;
    background-size: cover;
    content: " ";
    display: block;
    position: absolute;
    height: 2em;
    right: 0;
    top: -1.2em;
    width: 2em;
    z-index: 1;
    animation: blink 1s infinite;
  }
}
.photo {
  border-radius: 10%;
  width: 2.3em;
}
.info {
  display: flex;
  font-size: 0.95em;
